@import url('https://fonts.googleapis.com/css2?family=PT+Serif:ital,wght@0,400;1,700&family=Roboto:wght@500;700&display=swap');
.services {
  .article_container {
    // width: 80%;
    // max-width: 1000px;

    height: 608px;
    // height: auto;
    // min-height: 608px;
    margin: 0 auto;
    margin-top: 56px;
    background-color: #ffffff;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.15);
    display: flex;
  }

  .article_container:nth-child(2n) {
    flex-direction: row-reverse;
  }

  .article_container_img {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    min-width: 50%;
    background-color: #d4d9df;
    // background: url('https://source.unsplash.com/HW5013eyvlU');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    overflow: hidden;
  }

  .article_container_content {
    flex-grow: 1;
    padding: 55px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  h4 {
    font-size: 3rem;
    font-weight: 600;
    color: #33363b;
    font-family: 'Roboto', sans-serif;
    text-align: center;
  }

  .the {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
  }

  .the-decor {
    font-size: 1.125rem;
    font-weight: 600;
    font-family: 'PT Serif', serif;
    font-style: italic;
    margin: 0 16px;
  }

  p {
    font-family: 'PT Serif', serif;
    font-size: 1.125rem;
    color: #626a73;
    text-align: center;
    line-height: 36px;
  }

  .btn {
    padding: 20px 44px;
    outline: none;
    border: 1px solid #99a3b4;
    border-radius: 4px;
    text-align: center;
    font-size: 0.875rem;
    font-weight: 600;
    color: #33363b;
    text-transform: uppercase;
    cursor: pointer;
    font-family: 'Roboto', sans-serif;
    margin-top: 50px;
  }

  .btn:hover {
    background-color: #f0f0f0;
  }

  .divider {
    width: 60px;
    height: 4px;
    background-color: #33363b;
    margin: 40px 0;
  }

  .line {
    width: 35px;
    height: 1px;
    background-color: #33363b;
  }

  @media (max-width: 768px) {
    .article_container {
      flex-wrap: wrap;
      width: 100%;
      margin-top: 0;
    }
    .article_container_img {
      width: 100%;
      height: 608px;
    }
    .article_container_content {
      width: 100%;
    }
  }

  @media (min-width: 769px) and (max-width: 1024px) {
    .article_container {
      flex-wrap: wrap;
    }
    .article_container_img {
      width: 100%;
      height: 608px;
    }
    .article_container_content {
      width: 100%;
    }
  }
}
